

import React, {useEffect, useState} from 'react';
import { Connection } from '@solana/web3.js';
import { Provider } from '@project-serum/anchor';
import idl from '../idl/airdrops.json';
import { useWallet } from '@solana/wallet-adapter-react';
const anchor = require('@project-serum/anchor');

const opts = {
    preflightCommitment: "processed"
};


function useProgram() {
    let wallet = useWallet();

    const [program, setProgram] = useState(null);
    const [userInfo, setUserInfo] = useState(null);
    useEffect(() => {
        async function getProvider(wallet) {
            const network = process.env.REACT_APP_RPC_URL;
            const connection = new Connection(network, opts.preflightCommitment);
            const provider = new Provider(
                connection, wallet, opts.preflightCommitment,
            );
            let programId = idl.metadata.address,
                prog = new anchor.Program(idl, programId, provider),
                accounts = await prog.account.user.all();
		    for (let i=0; i< accounts.length; i++){
                let walletKey = accounts[i].account.walletKey;
                if (walletKey.toBase58() === wallet.publicKey.toBase58()){
                    setUserInfo(accounts[i]);
                    break;
                }
            }
            setProgram(prog)
        }
        if (wallet.connected && program === null){
            (async() => {
                await getProvider(wallet);
            })();
        }
        else{
            console.log("wallet disconnected")
            setUserInfo(null);
            setProgram(null);
        }

    }, [wallet.connected]); 
    return [program !== null, program, userInfo];
  }

export default useProgram;