import React from 'react';
import { Label, Image, Icon } from 'semantic-ui-react';
import "./horizontalStrip.css";

function HorizontalStrip(props) {
    let {labelClassName, textClassName, as, iconName, imageSrc, text, onClick, disabled} = props;
    return (
        <Label as={as} className={labelClassName + " genericClass"} onClick={() => onClick && !(disabled !== undefined ? disabled : false)? onClick() : null}>
            <Image src={imageSrc} ui={false} />
            <span className={textClassName +' genericText'}>{text}</span>
            <Icon className={iconName + " right"} color={iconName === "checkmark" ? "green" : iconName === "close" ? "red" : ""} />
        </Label>
    )
}

export default HorizontalStrip