import React, { useEffect, useState } from 'react'
import { Grid, Image, Pagination, Table } from 'semantic-ui-react';
import StatisticsCard from '../../common/statisticsCard';
import apiMockResponses from '../../utils/apiCalls';
import useWindowSize from '../../utils/windowSize';
import "./closed.css";
import catoLogo from "../../images/catoLogo.png";
import PopupComponent from './popupComponent';

const rowsPerPage = 7;

function Closed() {
    const [type, size] = useWindowSize();
    const [tableData, setTableData] = useState({
        initialized: false,
        data: []
    });
    const [paginatedTableData, setPaginatedTableData] = useState([]);
    const [activePage, setActivePage] = useState(1);

    const [cardData, setCardData] = useState({
        totalAirdropAmount: 0,
        listedAirdrop: 0,
        totalWinners: 0,
        initialized: false
    });
    const contextRef = React.useRef()
    const getTableData = async () => {
        let res = await apiMockResponses.getTableDataClosed();
        setTableData({
            data: res,
            initialized: true
        });
        let lengthInPageOne = res.length > rowsPerPage ? rowsPerPage : res.length;
        let pageOneData = res.slice(0, lengthInPageOne);
        setPaginatedTableData(pageOneData);
    }

    const getCardData = async () => {
        let res = await apiMockResponses.getCardData();
        setCardData({
            ...res,
            initialized: true
        });
    }

    const handleTableRowClick = (projectId) => {
        console.log(projectId);
    }

    const handlePageChange = (a) => {
        setActivePage(a.activePage);
        editPageOnPageChange(a.activePage);
    }

    const editPageOnPageChange = (pageNumber) => {
        let sliceIndex = pageNumber * rowsPerPage;
        let newData = tableData.data.slice(sliceIndex - rowsPerPage, sliceIndex);
        let originalLength = newData.length;
        if (newData.length < rowsPerPage) {
            for (let i = 0; i < rowsPerPage - originalLength; i++)
                newData.push({
                    asset: "",
                    airdropDate: null,
                    amount: null,
                    winners: null,
                    participated: null,
                    id: null
                })
        }
        setPaginatedTableData(newData);
    };

    useEffect(() => {
        if (!tableData.initialized)
            getTableData();
        if (!cardData.initialized)
            getCardData();
    }, [])


    return (
        <Grid className={'closedPageContainer ' + type} ref={contextRef}>
            {type === "big" ? <Grid.Row columns={3}>
                <Grid.Column className='cardColumn'>
                    <StatisticsCard value={cardData.totalAirdropAmount} label={"Total Airdrop Amount"} type={type} />
                </Grid.Column>
                <Grid.Column className='cardColumn'>
                    <StatisticsCard value={cardData.listedAirdrop} label={"Listed Airdrops"} type={type} />
                </Grid.Column>
                <Grid.Column className='cardColumn'>
                    <StatisticsCard value={cardData.totalWinners} label={"Total Winners"} type={type} />
                </Grid.Column>

            </Grid.Row>
                : <>
                    <Grid.Row columns={1}>
                        <Grid.Column>
                            <StatisticsCard value={cardData.totalAirdropAmount} label={"Total Airdrop Amount"} type={type} />
                        </Grid.Column>
                    </Grid.Row>
                    <Grid.Row columns={1}>
                        <Grid.Column>
                            <StatisticsCard value={cardData.listedAirdrop} label={"Listed Airdrops"} type={type} />
                        </Grid.Column>
                    </Grid.Row>
                    <Grid.Row columns={1}>
                        <Grid.Column>
                            <StatisticsCard value={cardData.totalWinners} label={"Total Winners"} type={type} />
                        </Grid.Column>
                    </Grid.Row>
                </>
            }
            <Grid.Row columns={1} className="tableContainer">
                <Table singleLine selectable unstackable>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell>Asset</Table.HeaderCell>
                            <Table.HeaderCell>Airdrop Date</Table.HeaderCell>
                            <Table.HeaderCell>Amount</Table.HeaderCell>
                            <Table.HeaderCell>Winners</Table.HeaderCell>
                            <Table.HeaderCell>Participated</Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>

                    <Table.Body>
                        {paginatedTableData.map((item, index) => {
                            return <PopupComponent
                                data={item}
                                contextComponent={contextRef}
                                component={<Table.Row disabled={!item.asset} onClick={() => handleTableRowClick(item.id)} key={index}>
                                    <Table.Cell collapsing className='assetName'>{item.asset ? <Image inline src={catoLogo} height="40" width="40" className='assetLogoTable' /> : null}
                                        {item.asset}
                                    </Table.Cell>
                                    <Table.Cell collapsing>{item.airdropDate}</Table.Cell>
                                    <Table.Cell collapsing>{item.amount}</Table.Cell>
                                    <Table.Cell collapsing>{item.winners}</Table.Cell>
                                    <Table.Cell collapsing>{item.participated}</Table.Cell>
                                </Table.Row>}>
                            </PopupComponent>
                        })}
                    </Table.Body>

                </Table>

            </Grid.Row>
            <Grid.Row columns={1} className="paginationContainer" centered>
                <Grid.Column textAlign='center'>
                    <Pagination totalPages={Math.ceil(tableData.data.length / rowsPerPage)}
                        firstItem={false} lastItem={false} activePage={activePage} onPageChange={(e, a) => handlePageChange(a)} />
                </Grid.Column>
            </Grid.Row>
        </Grid>
    )
}

export default Closed;