import React, { useState, useEffect } from 'react'
import { Link } from "react-router-dom";
import { Menu, Image, Dropdown } from 'semantic-ui-react';
import { useLocation } from "react-router-dom"
import "./topBar.css";
import logo from "../images/catoLogo.png";
import profilePlaceholder from "../images/profilePlaceholder.png";
import catoScoreSymbol from "../images/catoScore.png";

import {
    WalletMultiButton
} from '@solana/wallet-adapter-react-ui';
import apiMockResponses from '../utils/apiCalls';
import useProgram from '../utils/loadProgram';



function TopBar(props) {
    const { type } = props;
    const [catoScore, setCatoScore] = useState(null);
    const [isProgram, prog, userInfo] = useProgram();

    const getData = async () => {
        //let res = await apiMockResponses.getCatoScore();
        let score = 0;
        if (userInfo)
            score = userInfo.account.level;
        setCatoScore(score);
    }

    useEffect(() => {
        getData();

    }, [isProgram]);

    const location = useLocation();
    return location.pathname === "/" ? null : type === "big" ? (
        <Menu className='topMenuBar'>
            <Image src={logo} inline height="50" width="50" className='catoLogo' />
            <Menu.Item
                as={Link}
                name="live"
                active={location.pathname === "/live"}
                to="live"
            />
            <Menu.Item
                as={Link}
                name="closed"
                active={location.pathname === "/closed"}
                to="closed"
            />
            <Menu.Item
                as={Link}
                name="List Airdrop"
                active={location.pathname === "/list"}
                to="list"
            />
            <Menu.Menu position='right'>
                <Menu.Item>
                    <div className='catoScoreContainer'>
                        <span className='catoScoreNumber'>{catoScore}</span>
                        <Image src={catoScoreSymbol} inline height="20" width="25" className='catoScoreImage' />
                    </div>
                </Menu.Item>
                <Image as={Link} to="profile" src={profilePlaceholder} inline height="45" width="45" className='profilePlaceholder' />
                <Menu.Item>
                    <WalletMultiButton />
                </Menu.Item>
            </Menu.Menu>
        </Menu>
    ) :
        <>
            <Menu className='mobileMenuBar row1'>
                <Image src={logo} inline height="40" width="40" className='catoLogo' />
                <Menu.Item position='right'>
                    <WalletMultiButton />
                </Menu.Item>
            </Menu>
            <Menu className='mobileMenuBar row2'>
                <Menu.Item className='dropdownItem'>
                    <Dropdown options={[
                        {
                            text: "Live",
                            value: "live",
                            as: Link,
                            to: "/live"
                        },
                        {
                            text: "Closed",
                            value: "closed",
                            as: Link,
                            to: "/closed"
                        },
                        {
                            text: "Profile",
                            value: "profile",
                            as: Link,
                            to: "/profile"
                        },
                        {
                            text: "List",
                            value: "list",
                            as: Link,
                            to: "/list"
                        }
                    ]} defaultValue={location.pathname.slice(1)}/>
                </Menu.Item>
                <Menu.Item position='right'>
                    <div className='catoScoreContainer'>
                        <span className='catoScoreNumber'>{catoScore}</span>
                        <Image src={catoScoreSymbol} inline height="15" width="20" className='catoScoreImage' />
                    </div>
                </Menu.Item>
            </Menu>
        </>
}

export default TopBar;