import React from 'react';
import { Card, Image, Grid } from 'semantic-ui-react';
import './titleCard.css';

function TitleCard(props) {
    const { name, description, tokenAmount, participating, totalWinners, endsAt, status } = props.data;
    const { isActive } = props;

    function getEndDate(time){
        let endDate = new Date(time);
        endDate.toDateString();
        return endDate.getHours();
    }

    return (
        <Card className={isActive ? '' : 'title-active'}>
            <Card.Content textAlign='left'>
                <Image
                    src='https://images.ixigo.com/node_image/dyn/5ee766b5d7f62025ea83151b?amount=699'
                />
                <div className='heading'>{name}</div>
                <div className='desc'>{description}</div>
            </Card.Content>
            <Card.Content textAlign='left' className='items'>
            <Grid columns={5}>
                <Grid.Row>
                    <Grid.Column>
                        <p className='content'>Token Amount</p>
                        <p className='content-body'>{tokenAmount}</p>
                    </Grid.Column>
                    <Grid.Column>
                        <p className='content'>Participating</p>
                        <p className='content-body'>{participating}</p>
                    </Grid.Column>
                    <Grid.Column>
                        <p className='content'>Total Winners</p>
                        <p className='content-body'>{totalWinners}</p>
                    </Grid.Column>
                    <Grid.Column>
                        <p className='content'>Ends in</p>
                        <p className='content-body'>{`${getEndDate(endsAt)} Hours`}</p>
                    </Grid.Column>
                    <Grid.Column>
                        <p className='content'>Status</p>
                        <p className='content-body'>{status}</p>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            </Card.Content>
        </Card>
    )
}

export default TitleCard